export default {
    props: ['title', 'breadcrumbTitle'],
    async created() {
    await this.getConfig();
    },
    data() {
        return {
            mainUrl: this.$axiosUrl,
            Configs: [],
            errors: []
        }
    },

    methods: {
        async getConfig() {
        try {
        const confs = await this.$axios.get( this.$axiosUrl+"ayarlar" );
        this.Configs = confs.data
        } catch (error) { this.errors.push(error); }
        }
    },
};
